import { useState, useEffect } from 'react';
import AOS from "aos";

import { NavLink } from 'react-router-dom';
import './Popol.css'
import { GrClose } from "react-icons/gr";

function Popol() {

  useEffect(() => {
    AOS.init();
  },[])

  //     /    
  const [viewVisible, setViewVisible] = useState(null);

  //    
  const portfolios = [
    // 
    {
      title: "React      ",
      description: "     . 3 , 3   .",
      techStack: ["React", "Node.js", "REST API", "HTML", "CSS"],
      projectDescription: "     .",
      techUsed: "React, Node.js, Html, CSS, REST API",
      participants: "6(FE6, BE3)",
      period: "2023.08 ~ 2024.09",
      link: "",
      linkDescription: "", 
      details: [
        {
          title: "1. REST API    ",
          items: [
            "Node.js       ",
            "REST API         ",
            "         "
          ]
        },
        {
          title: "2. React   ",
          items: [
            "React      UI ",
            "  Figma      ",
            "UI          "
          ]
        },
        {
          title: "3.  API     ",
          items: [
            " API        ",
            "         ",
            "       "
          ]
        },
        {
          title: "4. ChatGPT   ",
          items: [
            "ChatGPT        Q&A ",
            "        ",
            "        "
          ]
        }
      ]
    },

    // 
    {
      title: "2023  ",
      description: "    ",
      techStack: ['Html',"CSS", "JavaScript", "PHP"],
      projectDescription: "  Figma  UI/UX    .",
      techUsed: "Html, CSS, JavaScript, PHP",
      participants: "1(FE1)",
      period: "2023.09 ~ 2023.11",
      link: "http://ksd2023graduate.com/",
      linkDescription: "2023  ",
      details: [
        { title: "1. Figma   ", 
          items: [
            "  Figma         "] 
          },
          { title: "2. UI/UX  ", 
            items: [
              "UI/UX    ,         ",
              "        "] 
            },
            { title: "3.  ", 
              items: [
                "         ",
              '  , Figma  CSS         . Design-to-Code       .'] 
              },
              { title: "4.  ", 
                items: [
                  "          .",
                  "PC/MOBILE/TABLET         ."
                ] 
                }
      ]
    },

    //     
    {
      title: "    ",
      description: "           ",
      techStack: ['React','Node.js','Html', "CSS", "JavaScript"],
      projectDescription: "           ,      .      ,          ,      .",
      techUsed: "Html, CSS, JavaScript",
      participants: "3(FE3, BE2)",
      period: "2024.06 ~ 2024.09",
      link: "",
      linkDescription: "",
      details: [
        {
          title: "1.      ",
          items: [
            "React           ",
            "     ,        ",
            "             ",
            "          ,       "
          ]
        },
        {
          title: "2. Node.js    ",
          items: [
            "Node.js   CRUD      ",
            "           ",
            "      ,          "
          ]
        }
      ]
      
    },

     // 
    {
      title: "RIDI ",
      description: "    ",
      techStack: [ 'Html', "CSS","JavaScript","SCSS"],
      projectDescription: "    .",
      techUsed: "Html, CSS, JavaScript, SCSS",
      participants: "1(FE1)",
      period: "2023.02 ~ 2023.02",
      link: "https://kimjavan.github.io/project5/",
      linkDescription: " ",
      details: [
        {
          title: "1. SCSS      ",
          items: [
            "SCSS   CSS       ",
            "      ",
            "           "
          ]
        },
        {
          title: "2.  API   ",
          items: [
            " API        ",
            "     "
          ]
        }
      ]
      
    },

    // 
    {
      title: "STEAM ",
      description: "    ",
      techStack: ['Html', "CSS", "JavaScript"],
      projectDescription: "    .",
      techUsed: "Html, CSS, JavaScript",
      participants: "3(FE3)",
      period: "2023.01 ~ 2023.02",
      link: "https://kimjavan.github.io/project4_G/sub1.html",
      linkDescription: " ",
      details: [
        {
          title: "1.      ",
          items: [
            "        ",
            "     ",
            "Steam        "
          ]
        },
        {
          title: "2.   UI ",
          items: [
            "        ",
            "   UI  "
          ]
        }
      ]
      
    },

    //  
    {
      title: "  ()",
      description: "     ",
      techStack: ["React", 'Html', "CSS", "JavaScript"],
      projectDescription: "     .",
      techUsed: "React, Html, CSS, JavaScript",
      participants: "1(FE1)",
      period: "2023.02 ~ 2023.04",
      link: "https://kimjavan.github.io/project6/",
      linkDescription: " ",
      details: [
        {
          title: "1. React      ",
          items: [
            "Redux      ",
            "React Bootstrap     UI ",
            "     ",
            "React Router       "
          ]
        }
      ]
    }
  ];

  //          
  const handleUnitClick = (index) => {
    setViewVisible(index);
  };

  //          
  const handleCloseClick = () => {
    setViewVisible(null);
  };

  return (
    <div>
      <div className='main-popol-wrap'>
        <div>
          <p className='popol-title' data-aos='fade-up'></p>
        </div>
        
        {/*  */}
        <div className='popol-btn-wrap' data-aos='fade-up'>
          <div className='popol-btn active'>
            <p></p>
          </div>
          <div className='popol-btn'>
            <p></p>
          </div>
          <div className='popol-btn'>
            <p></p>
          </div>
        </div>

        {/*   */}
        <div className='popol-unit-wrap'>
          {portfolios.map((portfolio, index) => (
            <div key={index} className='popol-unit-div' data-aos='fade-in' onClick={() => handleUnitClick(index)}>
              <div className='popol-unit-front-text'>
                <div className='po-title-text'>
                  <p>{portfolio.title}</p>
                </div>
                <div className='po-context-text'>
                  {portfolio.description}
                </div>
                <div className='front-index-wrap'>
                  {portfolio.techStack.map((tech, idx) => (
                    <div key={idx} className='front-index'>
                      <p className='front-inner-text'>{tech}</p>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* more-view-context */}
        {viewVisible !== null && (
          <div className='more-view-context'>
            <div className='more-scroll-div'>
              {/*  */}
              <div className='top-title-and-close-wrap'>
                <p className='po-more-title'>{portfolios[viewVisible].title}</p>
                <div className='close-btn' onClick={handleCloseClick}>
                  <GrClose />
                </div>
              </div>

              {/*  */}
              <div className='center-context-wrap'>
                <div className='center-unit'>
                  <div className='center-context-title'>
                    <p> </p>
                  </div>
                  <div className='center-context'>
                    <p>{portfolios[viewVisible].projectDescription}</p>
                  </div>
                </div>
                <div className='center-unit'>
                  <div className='center-context-title'>
                    <p> </p>
                  </div>
                  <div className='center-context'>
                    <p>{portfolios[viewVisible].techUsed}</p>
                  </div>
                </div>
                <div className='center-btm-unit-wrap'>
                  <div className='center-unit'>
                    <div className='center-context-title'>
                      <p></p>
                    </div>
                    <div className='center-context'>
                      <p>{portfolios[viewVisible].participants}</p>
                    </div>
                  </div>
                  <div className='center-unit'>
                    <div className='center-context-title'>
                      <p></p>
                    </div>
                    <div className='center-context'>
                      <p>{portfolios[viewVisible].period}</p>
                    </div>
                  </div>
                  {/*     */}
                  {portfolios[viewVisible].link && portfolios[viewVisible].linkDescription && (
                    <div className='center-unit'>
                      <div className='center-context-title'>
                        <p></p>
                      </div>
                      <div className='center-context'>
                        <NavLink to={portfolios[viewVisible].link}>
                          {portfolios[viewVisible].linkDescription}
                        </NavLink>
                      </div>
                    </div>
                  )}
                </div>
              </div>  

              {/*  */}
              <div className='btm-context-wrap'>
                <p className='btm-big-title'> </p>
                {portfolios[viewVisible].details.map((detail, index) => (
                  <div key={index} className='btm-sul-unit'>
                    <p className='btm-sul-title'>{detail.title}</p>
                    <ul>
                      {detail.items.map((item, idx) => (
                        <li key={idx}>{item}</li>
                      ))}
                    </ul>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default Popol;
